.splash{
    position: relative;
    width: 411px;
    height: 823px;
    background: radial-gradient(131.27% 136.09% at 54.62% 52.98%, rgba(237, 104, 86, 0.83) 0%, #8F1F1F 100%);
}


.fade-in{
    position: absolute;
    width: 115px;
    height: 75px;
    left: 148px;
    top: 393px;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 64px;
    line-height: 75px;  /* identical to box height */
    text-align: center;
    letter-spacing: 0.2em;
    color: white;
    z-index: 20;
}

.splash.display-none{
    position: relative;
    opacity: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    z-index: -10;
    text-align: center;
    line-height: 90px;
}

@keyframes fadeIn{
    to{
        opacity: 1;
    }
    
}

.fade-in{
    opacity:0;
    animation: fadeIn 1s ease-in forwards;
}